<template>
	<div id="order_detail">
		<div class="detail-header">
			<span>订单号：{{Order.tradeNo}}</span>
			<span class="fl-right negative" v-if="Order.status==0">已取消</span>
			<span class="fl-right active" v-else-if="Order.status==2">已付款</span>
			<span class="fl-right normal" v-if="Order.status!=0&&Order.status!=2">{{status[Order.status]}}</span>
		</div>
		<div class="detail-express">
			<div class="express-top" v-if="Order.status==6 || Order.status==7 || Order.status==8" @click="logisticsInfo">
				<img class="car-icon" src="../../assets/my_icon_car@2x.png" alt="">
				<span class="express-text" v-if="Order.status==7 || Order.status==8">快递已签收，感谢您在礼物飞购物</span>
				<span class="express-text" v-if="Order.status==6">查看快递详情</span>
				<img class="rt-arrow" src="../../assets/arrow_gray.png"/>
			</div>

			<div class="express-content" v-if="logistics.regionInfo && logistics.regionInfo.length > 0">
				<div>
					<img class="location-icon" src="../../assets/common_icon_location@2x.png"/>
				</div>
				<div class="express-info">
					<div class="express-user">
						<span>收货人：{{logistics.name}}</span>
						<span class="fl-right">{{logistics.mobile}}</span>
					</div>
					<div class="express-address">
						<span>收货地址：{{logistics.regionInfo[0].region.name}}{{logistics.regionInfo[1].region.name}}{{logistics.regionInfo[2].region.name}}{{logistics.street}}</span>
					</div>
				</div>
			</div>
			<img class="address-icon" src="../../assets/cart/address_bor.png"/>
		</div>
		
		<div class="detail-goods">
			<div class="shop-info">
				<span class="shop-label">{{Order.directSale==1?'自营':'加载'}}</span>
				<span>{{Order.orgName}}</span>
			</div>
			<div class="list-item" v-for="(good,index) in Order.goodsList">
				<div class="good-img">
					<img class="goods-img" :src="good.imagePath" alt=""/>
				</div>
				<!-- 商品信息 start -->
				<div class="content">
					<div class="goods-name">{{good.name}}</div>
					<div class="goods-norms">{{good.productSize}} <span v-if="good.packing.name != ''">({{good.packing.name}})</span></div>
					<div class="goods-prices">
            <div class="packprice">包装费：￥{{good.packPrice}}</div>
						<span class="price"><em class="price-icon">￥</em>{{good.discountPrice}}</span>
						<span class="num fl-right"><em>x</em>{{good.buyNum}}</span>
					</div>
				</div>
			</div>
		</div>
		
		<div class="detail-price">
			<div class="price-top">
				<div>
					<span>商品总价</span>
					<span class="fl-right">￥{{Order.goodsAmount}}</span>
				</div>
				<div>
					<span>优惠券</span>
					<span class="fl-right">-￥{{Order.reductionAmount}}</span>
				</div>
			</div>
			<div class="price-bottom">
				<span>实付款</span>
				<span class="price fl-right">￥{{Order.realAmount}}</span>
			</div>
		</div>
		
		<div class="detail-info">
			<div class="info-title">更多信息</div>
			<div class="info-content">
				<div>付款方式：{{payment.payType}}</div>
				<div>创建时间：{{payment.createTime}}</div>
				<div>付款时间：{{payment.payTime}}</div>
				<div>发货时间：{{payment.sendTime}}</div>
				<div>收货时间：{{payment.receiveTime}}</div>
			</div>
		</div>

		<div class="detail-footer">
			<div>
				<div @click="contactService(Order)">
					<i class="ser-icon"></i>
					<span>联系客服</span>
				</div>
			</div>
			<div>
				<template v-if="Order.status==0">
					<span class="footer-right" style="border: 0;"></span>
					<span class="footer-right" @click="showDel">删除订单</span>
				</template>
				
				<template v-if="Order.status==1">
					<span class="footer-right" @click="showCancel">取消订单</span>
					<span class="active footer-right" @click="getOrderUpdate(3)">立即支付</span>
				</template>
				
				<template v-if="Order.status==2 || Order.status==3">
					<span class="footer-right" @click="showCancel">取消订单</span>
					<span class="footer-right" @click="getOrderUpdate(5)">提醒发货</span>
				</template>
				
				<template v-if="Order.status==5 || Order.status==6">
					<span class="footer-right">申请售后</span>
					<span class="active footer-right" @click="confirm">确认收货</span>
				</template>
				
				<template v-if="Order.status==7">
					<span class="footer-right" >申请售后</span>
					<span class="active footer-right" @click="toSelectGoods">立即评价</span>
				</template>
				
				<template v-if="Order.status==8">
					<span class="footer-right" >申请售后</span>
					<span class="footer-right" @click="showDel">删除订单</span>
				</template>
			</div>
		</div>
	</div>
</template>

<script>
	
export default {
	data(){
		return {
			status:['已取消','待支付','已支付','待发货','发货中','已发货','待收货','待评价','已完成','','争议处理中','申请退货','退货中','退货已收货','退货完成'],
			logistics:{},
			payment:{},
			Order:{}
		}
	},
  components: {
  	
  },
  created(){
  	this.getOrderDetails()
  },
  methods:{
  	logisticsInfo(){
			let url = this.logistics.detailUrl
  		window.location.href = url
  	},
  	getOrderDetails(){
  		let orderId = this.$route.params.id
  		this.$Api.getOrderDetails(orderId).then((res)=>{
  			console.log(res)
  			if(res.q.s == 0){
  				this.Order = res.q.order
  				this.payment = res.q.payment
  				this.logistics = res.q.logistics
  			}
  		})
  	},
  	//删除订单
  	showDel(){
      this.$popup({
        content:'确认要删除选中的订单?'
      }).then((res) => {
      	if(res == 'sure'){
      		this.getOrderUpdate(2)
      	}
      })
    },
  	//取消订单
  	showCancel(){
      this.$popup({
        content:'确认要取消选中的订单?'
      }).then((res) => {
      	if(res=='sure'){
      		this.getOrderUpdate(1)
      	}
      })
    },
    //确认收货
    confirm(){
      this.$popup({
        content:'确认已经收到货物?'
      }).then((res) => {
        if(res == 'sure'){
        	this.getOrderUpdate(4)
        }
      })
    },
    //查看快递
    toKuaidi(){
    	window.location = "https://m.kuaidi100.com/result.jsp?nu=123123"
    },
    toSelectGoods(){
    	this.$router.push({name:'SelectGoods'});
    },
    getOrderUpdate(a){
    	let id = this.$route.params.id
    	this.$Api.getOrderUpdate(a,id).then((res)=>{
    		console.log(res)
    		if(res.q.s == 0){
    			if(a != 3){
    				this.$toast('操作成功')
    				setTimeout(()=>{
    					this.$router.replace({name:'OrderList'})
    				},1000)
    			}else if(a == 3){
    				let orderId = res.q.orderId
    				if(this.$call.testType().wechat){
					      this.$router.push({name: 'Cashier',query:{orderId:orderId,realAmount:this.Order.goodsAmount}})
      				}else{
      					let params = {
	      					action:0,
	      					amount:this.Order.goodsAmount,
	      					orderId : orderId
	      				}
      					checkOutCounter(params)
      				}
    			}
    		}else{
    			this.$toast(res.q.d)
    		}
    	})
    },
    //联系客服
    contactService(item){
    	if(this.$call.testType().wechat){
    		console.log(item)
			}else{
				contactCustomerService(item)
			}
    }
  },
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#order_detail{
		/*height: 100%;*/
		background: #f0f0f0;
		padding-bottom: 1.57rem;
		.detail-header{
			font-size: 0.37rem;
			width: 100%;
			height: 1.17rem;
			line-height: 1.17rem;
			padding: 0 0.4rem;
			color: #1a1a1a;
			background: #fff;
			.active{
				color: #fc4518;
			}
			.negative{
				color: #999;
			}
		}
		.detail-express{
			position:relative;
			margin-top: 0.27rem;
			color: #333;
			.express-top{
				/*padding: 0 0.4rem;*/
				position: relative;
				font-size: 0.37rem;
				height: 1.17rem;
				line-height: 1.17rem;
				background: #fff;
				color: #1a1a1a;
				border-bottom: 1px solid $border-color;
				.express-text{
					padding-left: 1.17rem;
				}
			}
			.car-icon{
				position: absolute;
				width: 0.56rem;
				height: 0.4rem;
				left: 0.4rem;
				top: 50%;
				transform: translateY(-50%);
			}
			.rt-arrow{
				@include rt-arrow-sm;
			}
			.express-content{
				display: flex;
				position: relative;
				font-size: 0.37rem;
				padding: 0.44rem 0;
				background: #fff;
				color: #333;
				.location-icon{
					position: absolute;
					width: 0.43rem;
					height: 0.53rem;
					top: 50%;
					left: 0.4rem;
					transform: translateY(-50%);
				}
				.express-info{
					padding-left: 1.17rem;
					padding-right: 0.4rem;
					.express-address{
						padding-top: 0.27rem;
					}
				}
			}
			.address-icon{
				position: absolute;
				bottom: 0;
				width: 100%;
			}
		}
		.detail-goods{
			margin-top: 0.27rem;
			background: #fff;
			.shop-info{
				position: relative;
				width: 100%;
				height: 1.17rem;
				line-height: 1.17rem;
				padding: 0 0.4rem;
				font-size: 0.37rem;
				color: #333;
				&:after{
					@include bottom;
				};
				.shop-label{
					color: $label-color;
				}
			}
			.list-item{
				position: relative;
				display: flex;
				padding: 0.21rem 0.32rem;
    		/*border-bottom: 1px solid #cccccc;*/
				/*&:after{
					@include bottom;
				};*/
				.good-img{
					width: 2.13rem;
					height: 2.13rem;
					border: 1px solid #dedede;
					border-radius: 0.11rem;
					.goods-img{
						width: 2.13rem;
						height: 2.13rem;
					}
				}
				.content {
					flex: 1;
					margin-left: 0.35rem;
					display: flex;
					justify-content: space-around;
					flex-direction: column;
					.goods-name {
						font-size: 0.37rem;
						color: $text-color;
						@include textflow-two;
					}
					.goods-norms {
						color: $text-color2;
						font-size: 0.32rem;
						@include textflow-one;
					}
					.goods-prices {
						.packprice{
							color: $price-color;
							font-size: 0.32rem;
						}
						.price {
							color: $price-color;
							font-size: 0.43rem;
							.price-icon {
								font-size: 0.32rem;
							}
						}
						.num {
							color: $text-color2;
							font-size: 0.32rem;
							>em {
								font-size: 0.24rem;
							}
						}
					}
				}
			}
		}
		.detail-price{
			background: #fff;
			margin-top: 0.27rem;
			font-size: 0.4rem;
			color: #333;
			.price{
				color: $price-color;
			}
			.price-top{
				padding:0.4rem;
				border-bottom: 1px solid $border-color;
				line-height: 1;
				>div{
					&:nth-of-type(1){
						padding-bottom: 0.32rem; 
					};
				}
			}
			.price-bottom{
				height: 1.17rem;
				line-height: 1.17rem;
				padding: 0 0.4rem;
			}
		}
		.detail-info{
			background: #fff;
			margin-top:0.27rem;
			.info-title{
				height: 1.17rem;
				line-height: 1.17rem;
				font-size: 0.4rem;
				padding: 0 0.4rem;
				color: #333;
				border-bottom: 1px solid $border-color;
			}
			.info-content{
				font-size: 0.35rem;
				color: #666;
				padding: 0.4rem;
				line-height: 1;
				>div{
					padding-bottom: 0.27rem;
					&:last-child{
						padding-bottom:0;
					};
				}
			}
		}
		.detail-footer{
			position: fixed;
			font-size: 0.37rem;
			display: flex;
			bottom: 0;
			width: 100%;
			height: 1.31rem;
			line-height: 1.31rem;
			z-index: 999;
			background: #fff;
			>div{
				text-align: center;
				flex: 1;
				>div{
					height: 0.67rem;
					line-height: 0.67rem;
					border-right: 1px solid $border-color;
					margin:0.32rem 0;
				}
				&:nth-of-type(1){
					color: #666;
				};
				&:nth-of-type(2){
					color: #7a7e83;
					height: 1.31rem;
					line-height: 1.31rem;
				};
			}
			.footer-right{
				display: inline-block;
				font-size: 0.37rem;
				height: 0.75rem;
				line-height: 0.72rem;
				width: 2.0rem;
				text-align: center;
				border:1px solid $border-color;
				border-radius: 0.37rem;
				&:last-child{
					margin-left:0.27rem;
				};
			}
			.ser-icon{
				display: inline-block;
				background-image: url('../../assets/ico_service.png');
				background-size: 0.59rem 0.65rem;
				width: 0.59rem;
				height: 0.65rem;
				vertical-align: text-bottom;
				margin-right: 0.21rem;
			}
			.active{
				color: $label-color;
				border-color: $label-color;
			}
		}
	}
</style>


